<template>
    <div class="setComponent">
        <div class="form-item">
            <label></label>
            <el-button type="primary" round size="mini" @click="addClick"></el-button>
        </div>
        <div class="form-item">
            <label></label>
            <el-button :disabled="obj.imgTextBox.length<=3" type="danger" round size="mini" @click="removeClick"></el-button>
        </div>    
        <div class="form-item">
            <label></label>
            <el-button size="mini" @click="titleClick('info')"></el-button>
        </div>
        <div class="form-item">
            <label></label>
            <el-button size="mini" @click="titleClick('title')"></el-button>
        </div>
        <div>
        <div class="form-item">
            <label></label>
            <el-button size="mini" @click="titleClick('desc')"></el-button>
        </div>
        </div>
        <div class="form-item">
            <label></label>
            <el-button size="small" type="primary" @click="iconClick"></el-button>
        </div>
        <div class="form-item">
            <label></label>
            <el-button size="small" type="primary" @click="uploadClick"></el-button>
        </div>
        
        <div class="form-item">
            <label></label>
            <el-input-number v-model="obj.imgWidth"></el-input-number>
        </div>
        <div class="form-item">
            <label></label>
            <el-input-number v-model="obj.timeLineWidth"></el-input-number>
        </div>
        <div class="form-item">
            <label></label>
            <el-select v-model="obj.timeLineColor" placeholder="">
                <el-option
                    v-for="(item, index) in bgColorOptions"
                    :key="index"
                    :label="item"
                    :value="item">
                    <span class="bgColor" :style="{background: item}"></span>
                </el-option>
            </el-select>
        </div>
        <div class="form-item">
            <label></label>
            <el-select v-model="obj.timeLineIconBg" placeholder="">
                <el-option
                    v-for="(item, index) in bgColorOptions"
                    :key="index"
                    :label="item"
                    :value="item">
                    <span class="bgColor" :style="{background: item}"></span>
                </el-option>
            </el-select>
        </div>
        <div class="form-item">
            <label></label>
            <el-select v-model="obj.animateImg" clearable placeholder="">
                <el-option-group
                    v-for="(group,i) in options"
                    :key="i"
                    :label="group.label">
                    <el-option
                        v-for="(item,itemIndex) in group.options"
                        :key="itemIndex"
                        :label="item.label"
                        :value="item.value">
                    </el-option>
                </el-option-group>
            </el-select>
        </div>
        <div class="form-item">
            <label></label>
            <el-select v-model="obj.animateTitle" clearable placeholder="">
                <el-option-group
                    v-for="(group,i) in options"
                    :key="i"
                    :label="group.label">
                    <el-option
                        v-for="(item,itemIndex) in group.options"
                        :key="itemIndex"
                        :label="item.label"
                        :value="item.value">
                    </el-option>
                </el-option-group>
            </el-select>
        </div>
    </div>
</template>
<script>
import { animateOptions } from '@/utils/animateOptions'
import { columnOptions, bgColorOptions } from '@/utils/selectOptions'
export default {
    name: 'ThreeColumns',
    props: {
        obj: {
            type: Object,

        }
    },
    data() {
        return {
            options: animateOptions,
            columnOptions: columnOptions,
            bgColorOptions: bgColorOptions
        }
    },
    methods: {
        butClick(msg) {
            this.$emit('butClick',msg)
        },
        uploadClick() {
            this.$emit('uploadClick',this.obj)
        },
        addClick() {
            this.obj.imgTextBox.push(JSON.parse(JSON.stringify(this.obj.imgTextBox[0])))
        },
        removeClick() {
            this.obj.imgTextBox.pop()
        },
        titleClick(type) {
            this.$emit('titleClick',[this.obj,type])
        },
        iconClick() {
            this.$message.info('')
        }
    }
}
</script>
<style lang="less">

</style>

