<template>
    <div class="setComponent">
        <div class="form-item">
            <label></label>
            <el-button type="primary" round size="mini" @click="addClick"></el-button>
        </div>
        <div class="form-item">
            <label></label>
            <el-button :disabled="obj.imgTextBox.length<=2" type="danger" round size="mini" @click="removeClick"></el-button>
        </div>    
       
        <div class="form-item">
            <label></label>
            <el-button size="mini" @click="titleClick('title')"></el-button>
        </div>
        <div>
        <div class="form-item">
            <label></label>
            <el-button size="mini" @click="titleClick('desc')"></el-button>
        </div>
        </div>
        <div class="form-item">
            <label></label>
            <el-button size="small" type="primary" @click="uploadClick"></el-button>
        </div> 
        <div class="form-item">
            <label></label>
            <el-input-number v-model="obj.fontSize"></el-input-number>   
        </div> 
    </div>
</template>
<script>
import { animateOptions } from '@/utils/animateOptions'
import { columnOptions } from '@/utils/selectOptions'
export default {
    name: 'ThreeColumns',
    props: {
        obj: {
            type: Object,

        }
    },
    data() {
        return {
            options: animateOptions,
            columnOptions: columnOptions,
            directionOptions: [
                {
                    label: '',
                    value: 'horizontal'
                },
                {
                    label: '',
                    value: 'vertical'
                }
            ]
        }
    },
    methods: {
        butClick(msg) {
            this.$emit('butClick',msg)
        },
        uploadClick() {
            this.$emit('uploadClick',this.obj)
        },
        addClick() {
            this.obj.imgTextBox.push(JSON.parse(JSON.stringify(this.obj.imgTextBox[0])))
        },
        removeClick() {
            this.obj.imgTextBox.pop()
        },
        titleClick(type) {
            this.$emit('titleClick',[this.obj,type])
        }
    }
}
</script>
